.footer {
  /* box-shadow: 0 1px 30px 0px #ccc; */
  position:fixed;
  bottom: 0;
  width: 100%;
  height: 70px;  
  background:url(data:image/png;base64,)no-repeat bottom center/100% !important;
}
.footer ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  width: 100%;
  font-size: 0.7em;
}
/* .footer a{
  display: block;
} */
.footer ul li {
  width: 20%;
  padding: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.footer ul li:nth-child(3) a{
  width: 100px;
  height: 100px;
}
.footer ul li:nth-child(3) {
  width: 20%;
}
.footer ul li:nth-child(3) img {
  width: 3em;
  height: 3em;
}
.footer ul li:nth-child(3) span {
  display: inline-block;
  background-color: #b43e3a;
  padding: 1.5em 1.5em;
  border-radius: 50%;
  position: absolute;
  top: -33%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 999;
  /* border: 1em solid #f8f8f8; */
  /* box-shadow: 0 11px 30px 0px #ccc; */
}
/* [class*=van-hairline]::after{
  border: none;
} */
.footer img {
  width: 2.8em;
}
.footer ul li.active a{
  color: #b43e3a;
}
/* 底部样式end */
